<template>
  <div :id="elementId" :style="getDivStyle"></div>
</template>

<script>
import { addListener, removeListener } from 'resize-detector'
import debounce from 'lodash/debounce'

/**
 * 
 */
const EVENTS = [
  'legendselectchanged',
  'legendselected',
  'legendunselected',
  'legendscroll',
  'datazoom',
  'datarangeselected',
  'timelinechanged',
  'timelineplaychanged',
  'restore',
  'dataviewchanged',
  'magictypechanged',
  'geoselectchanged',
  'geoselected',
  'geounselected',
  'pieselectchanged',
  'pieselected',
  'pieunselected',
  'mapselectchanged',
  'mapselected',
  'mapunselected',
  'axisareaselected',
  'focusnodeadjacency',
  'unfocusnodeadjacency',
  'brush',
  'brushselected',
  'rendered',
  'finished',
  'click',
  'dblclick',
  'mouseover',
  'mouseout',
  'mousemove',
  'mousedown',
  'mouseup',
  'globalout',
  'contextmenu'
]
export default {
  name: 'vue-echarts',
  props: {
    //  ECharts 
    initOptions: {
      type: Object
    },
    // ECharts 
    options: {
      type: Object
    },
    //  ECharts 
    theme: {
      type: [String, Object]
    },
    //  ECharts 
    group: {
      type: String
    },
    //  ECharts 
    autoresize: {
      type: Boolean,
      default: false
    },
    //  options prop  Vue  manual-update prop  true  options prop  ref  mergeOptions 
    manualUpdate: {
      type: Boolean,
      default: false
    },
    // 
    watchShallow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      lastArea: 0,
      chart: null,
      manualOptions: null,
      resizeHandler: null,
      // echarts element id
      elementId: new Date().getTime(),
      // 
      noDataOption: {
        title: {
          text: '',
          x: 'center',
          borderColor: '#ccc',
          top: '40%',
          textStyle: {
            color: '#888',
            fontStyle: 'normal',
            fontWeight: 'normal',
            fontFamily: 'sans-serif',
            fontSize: 28
          }
        }
      }
    }
  },
  // 
  created () {
    if (!this.manualUpdate) {
      // options
      this.$watch('options', (_new, old) => {
        if (!this.chart && _new) {
          this.init()
        } else {
          this.chart.setOption(_new, _new !== old)
        }
      }, { deep: !this.watchShallow })
    }
    const watched = ['theme', 'initOptions', 'autoresize', 'manualUpdate', 'watchShallow']
    watched.forEach(prop => {
      this.$watch(prop, () => {
        this.refresh()
      }, { deep: true })
    })
  },
  mounted () {
    // 
    if (this.options) {
      this.init()
    }
  },
  /**
   * 
   */
  activated () {
    if (this.autoresize) {
      this.chart && this.chart.resize()
    }
  },
  /**
   * chart
   */
  beforeDestroy () {
    if (this.chart) {
      this.destroy()
    }
  },
  // 
  computed: {
    // div
    getDivStyle: function () {
      if (!this.initOptions || !this.initOptions.height) {
        return 'height: 100%;'
      }
      return `height:${this.initOptions.height};`
    },
    // charts
    getHasData: function () {
      // 
      const option = this.manualOptions || this.options
      if (!option) {
        return false
      }
      if (!option.series || option.series.length === 0) {
        return false
      }
      let result = false
      for (let item of option.series) {
        if (item.data && item.data.length > 0) {
          result = true
          break
        }
      }
      return result
    }
  },
  methods: {
    /**
     * echart
     */
    delegateMethod: function (name, ...args) {
      if (!this.chart) {
        this.init()
      }
      return this.chart[name](...args)
    },
    // echartresize
    resize: function (options) {
      this.delegateMethod('resize', options)
    },
    // echartappendData
    appendData: function (params) {
      this.delegateMethod('appendData', params)
    },
    // echartdispatchAction
    dispatchAction: function (payload) {
      this.delegateMethod('dispatchAction', payload)
    },
    // echartconvertToPixel
    convertToPixel: function (finder, value) {
      this.delegateMethod('convertToPixel', finder, value)
    },
    // echartconvertFromPixel
    convertFromPixel: function (finder, value) {
      this.delegateMethod('convertFromPixel', finder, value)
    },
    // echartcontainPixel
    containPixel: function (finder, value) {
      this.delegateMethod('containPixel', finder, value)
    },
    // echartshowLoading
    showLoading: function (type, options) {
      this.delegateMethod('showLoading', type, options)
    },
    // echarthideLoading
    hideLoading: function () {
      this.delegateMethod('hideLoading')
    },
    // echartappendData
    getDataURL: function (options) {
      this.delegateMethod('getDataURL', options)
    },
    // echartappendData
    getConnectedDataURL: function (options) {
      this.delegateMethod('getConnectedDataURL', options)
    },
    clear: function () {
      this.delegateMethod('clear')
    },
    dispose: function () {
      this.delegateMethod('dispose')
    },
    // 
    mergeOptions: function (options, notMerge, lazyUpdate) {
      if (this.manualUpdate) {
        this.manualOptions = options
      }
      if (!this.chart) {
        this.init()
      } else {
        this.delegateMethod('setOption', options, notMerge, lazyUpdate)
      }
    },
    // el
    getArea () {
      return this.$el.offsetWidth * this.$el.offsetHeight
    },
    /**
     * 
     */
    init: function () {
      if (!this.chart && this.getHasData) {
        // chart
        const chart = window.echarts.init(this.$el, this.theme, this.initOptions)
        // const chart = echarts.init(document.getElementById(this.elementId), this.theme, this.initOptions)
        if (this.group) {
          chart.group = this.group
        }
        // chart.setOption(this.getOption, true)
        chart.setOption(this.manualOptions || this.options || {}, true)
        // 
        EVENTS.forEach(event => {
          if (this.$listeners[event]) {
            chart.on(event, params => {
              this.$emit(event, params)
            })
          }
        })
        // autoresize
        if (this.autoresize) {
          this.lastArea = this.getArea()
          this.resizeHandler = debounce(() => {
            if (this.lastArea === 0) {
              this.mergeOptions({}, true)
              this.resize()
              // this.mergeOptions(this.getOption || {}, true)
              this.mergeOptions(this.options || this.manualOptions || {}, true)
            } else {
              this.resize()
            }
            this.lastArea = this.getArea()
          }, 100, { leading: true })
          addListener(this.$el, this._resizeHandler)
        }
        this.chart = chart
      }
    },
    // charts
    destroy: function () {
      if (this.autoresize) {
        // 
        removeListener(this.$el, this._resizeHandler)
      }
      this.dispose()
      this.chart = null
    },
    // echart
    refresh: function () {
      if (this.chart) {
        this.destroy()
        this.init()
      }
    }
  }
}
</script>

